@font-face {
  font-family: "Montserrat Black";
  src: url("./assets/fonts/montserrat-black-webfont.woff2") format("woff2"),
    url("./assets/fonts/montserrat-black-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Merriweather italic";
  src: url("./assets/fonts/merriweather-italic-webfont.woff2") format("woff2"),
    url("./assets/fonts/merriweather-italic-webfont.woff") format("woff");
  font-weight: normal;
  font-style: italic;
}
@font-face {
  font-family: "Merriweather";
  src: url("./assets/fonts/merriweather-regular-webfont.woff2") format("woff2"),
    url("./assets/fonts/merriweather-regular-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Merriweather";
  src: url("./assets/fonts/merriweather-bold-webfont.woff2") format("woff2"),
    url("./assets/fonts/merriweather-bold-webfont.woff") format("woff");
  font-weight: bold;
  font-style: normal;
}

body {
  margin: 0;
  font-family: "Merriweather", serif !important;
  /* font-size: 1.25rem !important; */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.cover-container {
  margin-left: auto !important;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Montserrat Black", sans-serif !important;
  font-weight: 900 !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.logo {
  width: 2em !important;
  height: 2em !important;
}

.text-lime {
  color: #40ffb0 !important;
}

footer a,
footer a:focus,
footer a:hover {
  color: #40ffb0 !important;
}
